import React,{Component} from "react"
import http from "../services/http.Script"
class Register extends Component{
  state={
      data:{ 
        name: "", 
        email: "", 
        password: "", 
        role: "" 
      } ,
      conPass:"",
      error:{}
  }
  async register(url,obj){
    console.log(obj)
    let response = await http.post(url,obj)
    alert("User Successfull Added")
    window.location= "/home"
  }
  
  submit =()=>{
      console.log("hy")
    this.register("/register",this.state.data)
  }


  handleChange = (e) =>{
    const { currentTarget: input} = e
    let s1 = {...this.state}
    if(input.name=="conPass"){
        s1.conPass=input.value
    }else{
        s1.data[input.name]=input.value
    }
        s1.error=this.checkError(s1.data,s1.conPass)
    this.setState(s1) 
   }
   checkError=(data,conPass)=>{
    let errors={}
    errors.password=this.checkPassword(data.password)
    errors.conPass=this.checkConPass(conPass,data.password)
    errors.email=this.checkEmail(data.email)
    return errors
}
   checkPassword=(value)=>
    !value
     ?""
     :value.length<7
     ?"Password must be atleat 7 Charactor"
     :""
   checkConPass=(val1,val2)=>
      val1!=val2
        ? "Password does't Match"
        :""
   checkEmail=(val)=>
     !val
      ?"Email is Requid"
      :val.length<6
      ?"Email is not Valid"
      :""     

    showTextField= (lable,name,value,placeholder) =>{
        return (
        <div className="container">

            <lable>{lable}<span className="text-danger">*</span></lable>
          
                <div class="form-group">
                   <input
                     type={lable=="Password"?"password":"text"}
                      className="form-control"
                      name={name}
                      placeholder={placeholder}
                      value={value}
                      onChange={this.handleChange}
                   />              
                 </div>   
            
        </div>)
    }
    showRadio = (arr, name,val) => {
      
        return (
          <div className="container">
            
                

             <label>Role{<span className="text-danger">*</span>}</label>       
              {arr.map(op=>(
             
                <div className="form-check ">
                <input
                  className="form-check-input"
                  type="radio"
                  name={name}
                  value={op.value}
                  checked={val==op.value}
                  onChange={this.handleChange}
                />
                <label className="form-check-label">{op.name}</label>
              </div>
            ))}
          </div>
        );
      };
 
    render(){
        let {name,email,password,role} = this.state.data
        let {conPass,error} = this.state
        let arrRadio=[{name:"Student",value:"student"},{name:"Faculty",value:"faculty"}]
     return(
     <div className="container">
         <h3>Register</h3>
       {this.showTextField("Name","name",name,"Enter the Name")}
       <div class="form-group">
                {error.name?<span className="form-control text-danger" style={{width:"1050px",marginLeft:"14px",backgroundColor:"#f0c5c5"}}>{error.name}</span>:""}<br/>
       </div>
       {this.showTextField("Password","password",password,"Enter the Password")}
       <div class="form-group">
                {error.password?<span className="form-control text-danger" style={{width:"1050px",marginLeft:"14px",backgroundColor:"#f0c5c5"}}>{error.password}</span>:""}<br/>
               </div>
       {this.showTextField("Confirm Password","conPass",conPass,"Enter the re-Password")}
       <div class="form-group">
                {error.conPass?<span className="form-control text-danger" style={{width:"1050px",marginLeft:"14px",backgroundColor:"#f0c5c5"}}>{error.conPass}</span>:""}<br/>
               </div>
       {this.showTextField("Email","email",email,"Enter the Email")}
       <div class="form-group">
                {error.email?<span className="form-control text-danger" style={{width:"1050px",marginLeft:"14px",backgroundColor:"#f0c5c5"}}>{error.email}</span>:""}<br/>
               </div>
       {this.showRadio(arrRadio,"role",role)}
       <button className="btn btn-primary" onClick={()=>this.submit()}>Register</button>
     </div>)
    }
}
export default Register;