import React , {Component} from "react"
import { format } from 'date-fns';
import http from "../services/http.Script";
import auth from "../services/authServices";
class AddClasses extends Component{
    state={
        data:{course:"",time:"",endTime:"",topic:"",facultyName:""},
        dataArr:[],
        click:-1,
        courseArr:["ANGULAR","JAVASCRIPT","REACT","BOOTSTRAP","CSS","REST AND MICROSERVICES","NODE"]
    }
    async fetchData(){
        let {id} = this.props.match.params
        let user = auth.getUser()
        let {pathname} = this.props.location
        let response = await http.get(`/getFacultyClass/${user.name}`)
        console.log(response)
        let {data} = response     
        this.setState({dataArr:data})
    }
    componentDidMount() {
        this.fetchData();
      }
    
      componentDidUpdate(prevProps, prevState) {
        
        if (prevProps !== this.props) this.fetchData();
      }

    async postData(url,obj){
        console.log(obj)
        let response = await http.post(url,obj)
        this.props.history.push("/AllScheduledClasses")
    }
    async putData(url,id,obj){
        console.log("put",obj,id)
        let response = await http.put(`${url}/${id}`,obj)
        console.log(response)
        this.props.history.push("/AllScheduledClasses")
    }
    scheduledData=()=>{
        let s1 = {...this.state}
        let user = auth.getUser()
        s1.data.facultyName=user.name
        if(s1.click>=0){
            this.putData("/postClass",this.state.click,this.state.data)
        }else{
            this.postData("/postClass",this.state.data)
        }
    }

    handleChange = (e) =>{
        const { currentTarget: input} = e
        let s1 = {...this.state}
        s1.data[input.name]=input.value
        this.setState(s1) 
       }
    showDropDown = (arr,name,value) =>{
        return(
              <select className="form-control" name={name} value={value} onChange={this.handleChange}>
                  <option selected disabled value="">Select the Course</option>
                  {arr.map(st=><option>{st}</option>)}
                    </select>)
      }
      showTextField= (lable,name,value) =>{
        return (
        <div className="container">

            <lable>{lable}<span className="text-danger">*</span></lable>
          
                <div class="form-group">
                   <input
                     type={name=="topic"?"text":"time"}
                      className="form-control"
                      name={name}
                      value={value}
                      placeholder="Enter The Topic"
                      onChange={this.handleChange}
                   />              
                 </div>   
            
        </div>)
    }
    editClass=(id)=>{
        console.log(id)
        let s1 = {...this.state}
        let find = s1.dataArr.find(p=>id==p.classId)
        s1.click=id
        s1.data={ classId:find.classId,course:find.course,time:find.time,endTime:find.endTime,topic:find.topic}
        this.props.history.push("/scheduledClasses")
        this.setState(s1)
    }
    render(){
       let {course,time,endTime,topic} = this.state.data
       let {pathname} = this.props.location
       let {courseArr=[],dataArr=[],click} = this.state
        
        if(pathname=="/scheduledClasses"){
         return(
         <div className="container">
              <h3>{click>=0?"Edit Details of Class":"Schedule a Class"}</h3>
             {this.showDropDown(courseArr,"course",course)}<br/>
             {this.showTextField("Time","time",time)}<br/>
             {this.showTextField("End Time","endTime",endTime)}<br/>
             {this.showTextField("Topic","topic",topic)}<br/>
             <button className="btn btn-primary" onClick={()=>this.scheduledData()}>Scheduled</button>

         </div>)
        }else if(pathname="/AllScheduledClasses"){
            return(<div className="container ">
            <h2>All Scheduled Classes</h2>
            <div className="row text-center" style={{backgroundColor:"#c9d1d0",height:"35px"}}>
              <div className="col-3">Course Name</div>
              <div className="col-2">Start Time</div>
              <div className="col-2">End Time</div>
              <div className="col-3">Topic</div>
              <div className="col-2"></div>
            </div>
          
          {dataArr.map(op=>(
              <div className="row text-center border" style={{backgroundColor:"#f5dfa6",height:"35px"}}>
              <div className="col-3">{op.course}</div>
              <div className="col-2">{op.time}</div>
              <div className="col-2">{op.endTime}</div>
              <div className="col-3">{op.topic}</div>
              <div className="col-2"><button className="btn btn-secondary btn-sm" onClick={()=>this.editClass(op.classId)}>Edit</button></div>
            </div>
          ))}        <br/>
          <button className="btn btn-primary">Add New Classes</button>
    </div>)
        }

    }
}
export default AddClasses