import React , {Component} from "react";
import {Route,Redirect,Switch} from "react-router-dom";
import NavBar1 from "./navbar1";
import Login from "./login";
import Logout from "./logout";
import AllStudent from "./allStudent";
import Home from "./home";
import Register from "./register";
import StudentCourse from "./studentCourse";
import StudentDetails from "./studentDetails";
import AllClasses from "./allClasses";
import FacultyData from "./facultyData";
import AddClasses from "./addClasses";
class Main extends Component{
    
    render(){

       return(
           <div className="container">
               <NavBar1/>  
               <Switch> 
                    <Route path="/home" component={Home}/>    
                    <Route path="/login" component={Login}/>  
                    <Route path="/logout" component={Logout}/>   
                    <Route path="/register" component={Register} />   
                    <Route path="/allStudent" component={AllStudent}/> 
                    <Route path="/allFaculty" component={AllStudent}/>  
                    <Route path="/studentCourse" component={StudentCourse}/>
                    <Route path="/studentFaculity" component={StudentCourse}/> 
                    <Route path="/allClasses" component={AllClasses}/>    
                    <Route path="/allCourse" component={AllClasses}/>     
                    <Route path="/studentDetails" component={StudentDetails}/>  
                    <Route path="/coursesAssigned" component={AllClasses}/>
                    <Route path="/AllScheduledClasses" component={AddClasses}/> 
                    <Route path="/scheduledClasses"  component={AddClasses}/> 
                    <Redirect from="/" to="/login"/>
               </Switch>  
           </div>
       )
    }
}
export default Main;