import React,{Component} from "react"
import http from "../services/http.Script";
import auth from "../services/authServices";
class AllClasses extends Component{
    state={
        data:[]
    }

async componentDidMount(){
    let user = auth.getUser()
    console.log(user)
    let {pathname} = this.props.location
    let response=null
    if(pathname=="/allCourse")
      response = await  http.get(`/getStudentCourse/${user.name}`)
    else if (pathname=="/coursesAssigned") 
     response = await  http.get(`/getFacultyCourse/${user.name}`)
    else if(pathname=="/allClasses"){
      response = await  http.get(`/getStudentClass/${user.name}`)
     console.log("hy")
    }
    let { data }= response
    console.log(response)
    this.setState({dataArr:data})
}

    render(){
        let {dataArr=[]} = this.state
        let {pathname} = this.props.location
  if(pathname=="/allClasses"){
    return(<div className="container text-center">
    <h2>All Scheduled Classes</h2>
    <div className="row" style={{backgroundColor:"#c9d1d0",height:"35px"}}>
      <div className="col-2">Course Name</div>
      <div className="col-2">Start Time</div>
      <div className="col-2">End Time</div>
      <div className="col-2">Faculty Name</div>
      <div className="col-2">Topic</div>
    </div>
  
  {dataArr.map(op=>(
      <div className="row border" style={{backgroundColor:"#f5dfa6",height:"35px"}}>
      <div className="col-2">{op.course}</div>
      <div className="col-2">{op.time}</div>
      <div className="col-2">{op.endTime}</div>
      <div className="col-2">{op.facultyName}</div>
      <div className="col-2">{op.topic}</div>
    </div>
  ))}        
</div>)
  }else{
       return(<div className="container text-center">
            <h2>Courses Assigned</h2>
            <div className="row" style={{backgroundColor:"#cef2ed",height:"35px"}}>
              <div className="col-3">Course Id</div>
              <div className="col-3">Course Name</div>
              <div className="col-3">Course Code</div>
              <div className="col-3">Course Descripation</div>
            </div>
          
          {dataArr.map(op=>(
              <div className="row border" style={{backgroundColor:"#59bdae",height:"40px"}}>
              <div className="col-3">{op.courseId}</div>
              <div className="col-3">{op.name}</div>
              <div className="col-3">{op.code}</div>
              <div className="col-3">{op.description}</div>
            </div>
          ))}        
       </div>)}
    }
}
export default AllClasses;