import React,{Component} from "react"
import http from "../services/http.Script";
import { format } from 'date-fns';
import auth from "../services/authServices";
class StudentDetails extends Component{
state={
    data:{name: "", dob: "", gender: "", about:""},
    dateformat:{date:"",month:"",year:""}
}
   async  componentDidMount(){
     let user = auth.getUser()
     let response = await http.get(`/getStudentDetails/${user.name}`)
     let { data } = response
     console.log("data",data)
     this.setState({data:data})
   }
   async postData(url,obj){
     let response = await http.post(url,obj)
     this.props.history.push("/allCourse")
   }
   handleChange = (e) =>{
    const { currentTarget: input} = e
    let s1 = {...this.state}
    if(input.name=="date"||input.name=="month"||input.name=="year"){
        s1.dateformat[input.name]=input.value
        console.log("handle",s1.dateformat.year)
        s1.data.dob=`${s1.dateformat.year}-${s1.dateformat.month}-${s1.dateformat.date}`
      }
    s1.data[input.name]=input.value
    this.setState(s1) 
   }
    showRadio = (arr, name,val) => {
        return (
          <div className="container">
              <div className="row">
             <div className="col-4">Gender{<span className="text-danger">*</span>}</div>       
              {arr.map(op=>(
               <div className="col-4">
                <div className="form-check  form-check-inline">
                <input
                  className="form-check-input"
                  type="radio"
                  name={name}
                  value={op.value}
                  checked={val==op.value}
                  onChange={this.handleChange}
                />
                <label className="form-check-label">{op.name}</label>
                </div>
              </div>
            ))}
          </div></div>
        );
      };
    showDropDown = (arr,name,value,selVal) =>{
        return(
              <select className="form-control" name={name} value={value} onChange={this.handleChange}>
                  <option selected disabled value="">{selVal}</option>
                  {arr.map(st=><option>{st}</option>)}
                    </select>)
      }
    showTextField= (lable,name,value,placeholder) =>{
        return (
        <div className="container">
          <div class="form-group">
             <label >{lable}<span className="text-danger">*</span></label>
           <textarea 
             className="form-control" 
             rows="3"
             name={name}
             value={value}
             onChange={this.handleChange}
             >
             </textarea>
         </div>
            
        </div>)
    }  
    addDetails = ()=>{
      this.postData("/postStudentDetails",this.state.data)
    }
 
    render(){
        let genderArr=[{name:"Male",value:"male"},{name:"Female",value:"female"}]
        let {date,year,month} = this.state.dateformat
        let {gender,about,dob} = this.state.data
        let index=[]
        console.log("dob",dob,year)
        for(let i=0;i<=dob.length;i++){
          if(dob[i]=="-")
          index.push(i)
        }
        for(let i=0;i<dob.length;i++){
          if(i>-1&&i<4&&year==""){  
            year=year+dob[i]
          }else if(i>4&&i<7&&month==""){
            console.log(dob)
            month=""
            month=month+dob[i]
          }else if(i>7&&i<10&&date==""){
            date=""
            date=date+dob[i]
          }       
        }console.log("year",year)
        let yearArr=[]
        let dateArr=[]
        let monthArr=[]
        for (var i = 1; i <= 12; i++) {
          monthArr.push(i)
     }
        for (var i = 1950; i <= 2021; i++) {
             yearArr.push(i)
        }
        for (var i = 1; i <= 31; i++) {
          dateArr.push(i)
     }
       return(
       <div className="container">
           <h3>Student Details</h3>
            {this.showRadio(genderArr,"gender",gender)}<br/>
            <label>Date-of-Birth<span className="text-danger">*</span></label>
            <div className="row">
                <div className="col-4">{this.showDropDown(yearArr,"year",year,"Select Year")}</div>
                <div className="col-4">{this.showDropDown(monthArr,"month",month,"Select Month")}</div>
                <div className="col-4">{this.showDropDown(dateArr,"date",date,"Select Date")}</div>
            </div><br/>
            {this.showTextField("About MySelf","about",about,"enetr")}<br/>
            <button className="btn btn-primary" onClick={()=>this.addDetails()}>Add Details</button>

       </div>)
    }
}
export default StudentDetails;