import React,{Component} from "react"
import http from "../services/http.Script";
import auth from "../services/authServices";
class FacultyData extends Component{
   
 
    state={
        dataArr:[]
    }
    async componentDidMount(){
        let user = auth.getUser()
        let response = await  http.get(`/getFacultyClass/${user.name}`)
        let {data} = response
        this.setState({dataArr:data})
     }

     editClass=(id)=>{
         this.props.history.push(`/addClasses/${id}`)
     }
   
    render(){
        let {dataArr} = this.state
        return(<div className="container ">
        <h2>All Scheduled Classes</h2>
        <div className="row text-center" style={{backgroundColor:"#c9d1d0",height:"35px"}}>
          <div className="col-3">Course Name</div>
          <div className="col-2">Start Time</div>
          <div className="col-2">End Time</div>
          <div className="col-3">Topic</div>
          <div className="col-2"></div>
        </div>
      
      {dataArr.map(op=>(
          <div className="row text-center border" style={{backgroundColor:"#f5dfa6",height:"35px"}}>
          <div className="col-3">{op.course}</div>
          <div className="col-2">{op.time}</div>
          <div className="col-2">{op.endTime}</div>
          <div className="col-3">{op.topic}</div>
          <div className="col-2"><button className="btn btn-secondary btn-sm" onClick={()=>this.editClass(op.courseId)}>Edit</button></div>
        </div>
      ))}        <br/>
      <button className="btn btn-primary">Add New Classes</button>
</div>)
    }
}
export default FacultyData;