import { Component } from "react";
import http from "../services/http.Script";
class StudentCourse extends Component{
 
    state={
        dataArr:[],click:1,
        stdName:{},
        form:{courseId:"",name:"",code:"",students:[],description:"",faculty:[]}
    }
 
async componentDidMount(){
    let {pathname} = this.props.location
    let response1=null
    let response=null
    if(pathname=="/studentCourse"){
          response = await http.get("/getCourses")
          response1 = await http.get("/getStudentNames")
    }else{
        response = await http.get("/getCourses")
        response1 = await http.get("/getFacultyNames")
    }
   
    let {data}= response
    console.log(response1)
   this.setState({dataArr:data,stdName:response1})
}
async putData(obj){
    console.log(obj)
    let response = await http.put("/putCourse",obj)
    console.log(response)
    this.setState({click:1})
}

 editStudent=(id)=>{
   let s1 = {...this.state}
   let find = s1.dataArr.find(op=>op.courseId==id)
   s1.form={courseId:find.courseId,name:find.name,code:find.code,students:find.students,description:find.description,faculty:find.faculty}
  console.log(s1.form)
   s1.click=0
   this.setState(s1)
 }
 handleChange = (e) =>{
     let {currentTarget:input} = e;
    let s1 = {...this.state}
    if(input.name=="students"||input.name=="faculty"){
        s1.form[input.name]=(this.updateCBs(s1.form[input.name],input.checked,input.value))
    }
    else{
        s1.form[input.name]=input.value
    }
    
   this.setState(s1)
 }
  updateCBs = (inpValue, checked , value)=>{
      let inpArr = inpValue 
      if(checked){
          inpArr.push(value)
      }else{
          let index = inpArr.findIndex((ele) => ele===value);
          if(index >=0) inpArr.splice(index,1);
      }
      return inpArr
  }

showTextField= (lable,name,value,placeholder) =>{
    return (
    <div className="container">

        <lable>{lable}<span className="text-danger">*</span></lable>
      
            <div class="form-group">
               <input
                 type={lable=="Password"?"password":"text"}
                  className="form-control"
                  name={name}
                  placeholder={placeholder}
                  value={value}
                  onChange={this.handleChange}
               />              
             </div>   
        
    </div>)
}
showCheckBox=(Arr,value,name)=>{
    console.log(value)
    return(  <div className="container ">
  <label>StudentsName<span className="text-danger">*</span></label><br/>
    {Arr.map((crc)=>(
  <div className="">
      <div className="form-check ">
      <input
      className="form-check-input"
      type="checkbox"
      name={name}
      value={crc}
      checked={value.find((val)=>val==crc)}
      onChange={this.handleChange}
      />
      <label className="form-check-label">{crc}</label></div>
  </div>
    ))}</div>)
}

updateData=()=>{    
    this.putData(this.state.form)
}

    render(){
        let {dataArr,click,form,stdName} = this.state
        let {pathname}= this.props.location
        let {data} = stdName
        console.log(data)
        if(click==1){
        return(
            <div className="container">
                <h2>{pathname=="/studentCourse"?"Add Student to the Course":"Add Faculty to the Course"}</h2>
               <div className="row bg-dark text-light text-center">
                   <div className="col-1">CourseId</div>
                   <div className="col-2">Name</div>
                   <div className="col-2">Course Code</div>
                   <div className="col-3">Description</div>
                   <div className="col-1">{pathname=="/studentCourse"?"Students":"Faculity"}</div>
                   <div className="col-3"></div>
               </div>
            {dataArr.map(op=>(
                      <div className="row bg-warning border text-center">
                      <div className="col-1">{op.courseId}</div>
                      <div className="col-2">{op.name}</div>
                      <div className="col-2">{op.code}</div>
                      <div className="col-3">{op.description}</div>
                      <div className="col-1">{pathname=="/studentCourse"?op.students.map(p=><span>{p} ,</span>):op.faculty.map(p=><span>{p} ,</span>)}</div>
                      <div className="col-3"><button className="btn btn-primary mt-1 btn-sm" onClick={()=>this.editStudent(op.courseId)}>Edit</button></div>
                  </div>
            ))}
            </div>)}
            else{
                return(<div className="container">
                     <h2>Add Student to the Course</h2><hr/>
                     <h3>Edit Student</h3>
                    {this.showTextField("Name","name",form.name,"Enter the name")}<br/>
                    {this.showTextField("Course Code","code",form.code,"Enter the  Course Code")}<br/>
                    {this.showTextField("Description","description",form.description,"Enter the Description")}<br/>
                    {pathname=="/studentCourse"
                    ?this.showCheckBox(data,form.students,"students")
                    :this.showCheckBox(data,form.faculty,"faculty")}<br/>
                  
                    <button className="btn btn-primary" onClick={()=>this.updateData()}>Update</button>
                </div>)
            }
    }
}
export default StudentCourse;